.sidebar-container {
    padding: 0;
    margin-left: 22px;
    margin-right: 10px;
    

}


.game-info-childs {
    display: flex;
    outline: 1.5px solid rgb(255, 255, 249);
    border-radius: 10px;
    margin-top: 5px;
    margin-bottom: 10px;
}

/*
    DIV #1
*/
.child-one {
    text-align: center;
    height: 13%;
    /*background-color: #173f5f;*/
    background-color: transparent;
}

.timer {
    color: white;
    font-weight: lighter;
    font-size: 70px;
    width: fit-content;
    height: fit-content;
    margin-left: auto;
    margin-right: auto;
    white-space: nowrap;

}

/*
    DIV #2
*/
.child-two {
    position: relative;
    height: 58%;
    background-color: transparent;
    display: block;
}

.control-card {
    position: relative;
    display: flex;
    height: 20%;
    width: 100%;
}
.control-card-child {
    display: flex;
    margin: auto;
}


.maze-info-container {
    height: 58%;
    width: 100%;
    display: flex;
    box-sizing: border-box;
    justify-content: space-around;
}

.multiplayer-players {
    background-color: aliceblue;
    width: 98%;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    outline: 2px solid black;
    border-radius: 10px;
}

.character-info {
    box-sizing: border-box;
    position: relative;
    background-color: aliceblue;
    width: 48%;
    margin-left: 5px;
    margin-right: 3px;
    outline: 2px solid black;
    border-radius: 10px;
    margin-top: 10px;
    overflow: visible;
}
.environment-info {
    box-sizing: border-box;
    position: relative;
    background-color: aliceblue;
    width: 48%;
    margin-left: 3px;
    margin-right: 5px;
    outline: 2px solid black;
    border-radius: 10px;
    margin-top: 10px;
    overflow: visible;
}
.maze-info-card {
    display: flex;
    width: max-content;
    margin: auto;
    height: max-content;
    margin-bottom: -10px;
}

ul {
    list-style-type: none;
    margin: auto;
    padding: 0;
    overflow: hidden;
    
}
ul.character-list {
    margin-left: 0px;
    margin-right: -25px;
    width: fit-content;
}

li {
    margin-left: 8px;
    display: flex;
    margin-bottom: -15px;
}

li.characters {
    /* margin-left: 0px; */
    width: max-content;
}

.info-image {
    width: 30px;
    height: 30px;
    
    object-fit: contain;
}

.info-name {
    font-size: 20px;
    font-weight: lighter;
    margin-top: auto;
    margin-left: 2px;
    word-wrap: break-word;
}


.info-title {
    margin-top: auto;
    font-size: 20px;
    margin-bottom: 10px;
}


.control-image1 {
    width: 120px;
    height: 100%;
    object-fit: contain;
}

.control-image2 {
    width: 120px;
    height: 100%;
    object-fit: contain;
}

.control-text {
    color: white;
    font-size: 30px;
    width: max-content;
    font-weight: lighter;
}

.info-box {
    position: relative;
    height: 36%;
    width: 96%;
    background-color: aliceblue;
    margin: auto;
    border-radius: 10px;
}

.info-text {
    color: black;
    font-weight: lighter;
    margin-left: 10px;
    margin-right: auto;
}

/* MULTIPLAYER PLAYER INFO STYLES  */

.character-list-m {
    /* background-color: aquamarine; */
    width: 100%;
}

.item-m {
    margin-bottom: 10px;
    /* background-color: bisque; */
    
}

.item-hide {
    opacity: 40%;
}

.player-image-m {
    width: 50px;
    height: 50px;
    object-fit: contain;
    margin-left: 25px;
}

.player-name-m {
    font-size: 35px;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 10px;
    font-weight: lighter;
   
}

.ready-indicator {
    width: 20px;
    height: 20px;
    background-color: #121212;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 15px;
    outline: 1px solid black;
    border-radius: 10px;
}

.ready {
    background-color: rgb(3, 243, 3);
}

.not-ready {
    background-color: red;
}

.host-indicator {
    width: 32px;
    height: 32px;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 15px;

}



/*
    DIV #3
*/
.child-three {
    height: 13%;
    background-color: aliceblue;
}

.training {
    background-color: transparent;
    outline: none;
}

.gameMessage {
    color: black;
    font-weight: lighter;
    margin-left: auto;
    margin-right: auto;
}





/*
    DIV #4
*/
.child-four {
    height: 10%;
    background-color: transparent;
    outline: none;
}

.game-buttons {
    margin-top: auto;
    margin-bottom: auto;
   
    font-size: 25px;
    border-radius: 15px;
    background-color: aliceblue;
    padding: 18px;
    width: 180px;
    
    /*width: 50%;
*/
}

.enabled-button:hover {
    background-color: rgb(0, 187, 187);
    outline: 2px solid white;
}

.enabled-training-button:hover {
    background-color: rgb(178, 0, 194);
    outline: 2px solid white;
}

.frozen-training-button {
    background-color: rgb(178, 0, 194);
    outline: 2px solid white;
}

.left {
    margin-right: 3px;
}

.right {
    margin-left: 3px;
}

.disabled-button {
    opacity: 30%;
}


/*
    DIV #5
*/


.child-five {
    height: 10%;
    outline: none;

}


.ready-button-m {
    position: relative;
    width: 100%;
    border-radius: 15px;
    margin-left: auto;
    margin-right: auto;
}

.ready-enabled:hover {
    background-color: rgb(0, 216, 90);
    outline: 2px solid white;
}

.ready-disabled {
    opacity: 30%;
}


@media screen and (max-width: 1150px) {
    .game-buttons {
        width: 150px;
        font-size: 20px;
    }

    .info-title {
        font-size: 18px;
    }

    .info-image {
        width: 25px;
        height: 25px;
    }

    .info-name {
        font-size: 18px;
    }

    .info-text {
        font-size: 28px;
    }

    
}

@media screen and (min-width: 1921px) {

    .timer {
        font-size: 105px;
    }

    .game-buttons {
        width: 275px;
        font-size: 40px;
        padding: 25px;
    }

    .info-title {
        font-size: 25px;
    }

    .info-image {
        width: 45px;
        height: 45px;
    }

    .info-name {
        font-size: 30px;
    }

    .info-text {
        font-size: 45px;
    }

    .ready-button-m {
        font-size: 45px;
    }


    .player-image-m {
        width: 80px;
        height: 80px;
    }

    .player-name-m {
        font-size: 50px;

    }

    .ready-indicator {
        width: 30px;
        height: 30px;
        border-radius: 15px;
    }

    .host-indicator {
        width: 45px;
        height: 45px;
    }
}


@media screen and (max-height: 800px) {
    .game-buttons {
            width: 150px;
            font-size: 20px;
        }
    
        .info-title {
            font-size: 18px;
        }
    
        .info-image {
            width: 25px;
            height: 25px;
        }
    
        .info-name {
            font-size: 18px;
        }
    
        .info-text {
            font-size: 28px;
        }
    
}